import axios from 'axios';
import {useState, useEffect} from 'react';
import _ from 'lodash';
import Loading from '../Loading';
import Filter from '../Filter';
import User from './User';

const UsersList = (props) => {
	const [users, setData] = useState([]);
	const [filterUsers, setFilterData] = useState([]);
	const [query, setQuery] = useState('');
	const [prevQuery, setPrevQuery] = useState('');

	const fetchData = async () => {
		const result = await axios(
			'https://jsonplaceholder.typicode.com/users',
		);

		setData(result.data);
	};

	const changeQueryString = evt => {
		const currentString = evt.target.value;
		setQuery(currentString);
	};
	
	useEffect(() => {
		fetchData();
	}, []);

	useEffect(() => {
		if(query !== prevQuery) {
			setPrevQuery(query);
			const f = _.filter(users, user => user.name.match(/`${query}`/gi))
			setFilterData(f);
		}
		
	});
	
	return (
		<>
			<Filter changeQueryString={changeQueryString}/>
			<ul>
				{users.length > 0 && !filterUsers.length &&
					users.map(it =>
						<User 
							user={it} 
							key={it.id}
						/>
					)
				}

				{filterUsers.length > 0 && filterUsers.map(it =>
						<User 
							user={it} 
							key={it.id}
						/>
				)}
				<Loading isShown={!Boolean(users.length)}/>
			</ul>
		</>
	)
};

export default UsersList;